  }
}
.ui-button {
  padding: 9px 10px;
  text-transform: uppercase;
  border-radius: 4px;
  text-decoration: none;
  font-weight: 600;
  transition: color 200ms;
  transition-property: background-color, color;
  border: 0;
  font-size: 16px;
  text-align: center;
  font-weight: 600;
  cursor: pointer;
}

.ui-button--rounded {
  border-radius: 5px;
}

.ui-button--bordered-green {
  padding: 7px 10px;
  border-color: #1DD1A1;
  color: #1DD1A1;
  border: 2px solid;
}

.ui-button--bordered-green:hover {
  background-color: #1DD1A1;
  color: #1DD1A1;
}


.ui-button--contained-green {
  background-color: #1DD1A1;
  color: #fff;
}

.ui-button--contained-green:hover {
  background-color: #00b894;
  border-color: #00b894;
  color: #fff;
}





.user-login {
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  padding-top: 150px;
  
  
}


@media (max-width: 600px){
  .user-login {
    padding: 70px 60px 0 60px;
  }
 
}

@media (min-width: 600px) and (max-width: 1000px){
  .user-login {
    padding: 200px 200px 0 200px;
  }

}
@media (min-width: 1000px) and (max-width: 1280px) {
  .user-login {
    padding: 100px 380px 0 380px; 
  }

}
@media only screen and (min-width: 1281px) {
  .user-login {
    padding: 100px 500px 0 500px; 
    max-width: 1280px;
  }
}



.user-login__title {
  font-size: 40px;
  color: #0ABDE3;
  margin-bottom: 50px;
  text-align: center;
}

.user-login__form-control {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
#form-control {
  height: 30px;
  border-radius: 5px;
}

.user-login__form-control label {
  margin-bottom: 5px;
  color: #979797;
  font-size: 16px;
}

.form-container {
  display: flex;
  flex-direction: column;
}
.form-container p{
  color: #fff;
} 

.user-login__form-control input {
  border-radius: 4px;
  border: 1px solid #BBBBBB;
  height: 36px;
  padding: 5px 10px;
}

.user-login__form-control input:focus {
  outline: 0;
  border-color: #5988df;
}

.user-login__submit-button {
  width: 100%;
  margin-top: 30px;
}

.user-login__error {
  color: red;
}

